* {
  font-family: "Montserrat", sans-serif !important;
  font-weight: 500;
  padding: 0;
  margin: 0;
}
body {
  background-color: #f0f0f0;
}
.naoSelecionavel {
  -webkit-touch-callout: none; /* iPhone OS, Safari */
  -webkit-user-select: none; /* Chrome, Safari 3 */
  -khtml-user-select: none; /* Safari 2 */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* IE10+ */
  user-select: none; /* Possível implementação no futuro */
  /* cursor: default; */
}

::-webkit-scrollbar {
  width: 8px;
  height: 8px;
  background-color: rgba(250, 250, 250, 0);
  border: none;
}
::-webkit-scrollbar-thumb {
  background-color: #bbbbbb;
  border-radius: 8px;
}
/*-----------------NAVBAR-----------------*/
.logo-menu-mobile {
  display: none;
}
#interactiveMenu {
  display: none;
}
.a-item-nav {
  text-decoration: none;
  color: #f2f2f2;
  font-size: 12px;
  font-weight: 600;
}
.a-item-nav:hover {
  color: #dba111;
}
nav {
  z-index: 1001;
  background-color: #004272;
  width: 100%;
  position: fixed;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.3);
}
.navbar {
  display: flex;
  padding-top: 0;
  padding-bottom: 0;
}
.menu-nav {
  text-align: center;
  list-style: none;
  margin-top: auto;
  margin-bottom: auto;
}
.item-nav {
  padding: 18px;
  display: inline-block;
  text-decoration: none;
}
.a-item-nav {
  padding: 20px;
}
/*-----------------NAVBAR-----------------*/

/*-----------------MOUSE ANIMATION-----------------*/
.scroll-text {
  text-align: center;
  position: absolute;
  bottom: 10%;
  width: 100%;
  color: #004272;
  font-weight: 400;
  letter-spacing: 3px;
  font-size: 14px;
}
.scroll-downs {
  position: absolute;
  bottom: 2%;
  margin-bottom: 20px;
  width: 100%;
  height: 32px;
}
.mousey {
  width: 4px;
  margin: 10px auto;
  padding: 10px 10px;
  height: 18px;
  border: 2px solid #004272;
  border-radius: 25px;
  opacity: 0.75;
  box-sizing: content-box;
}
.scroller {
  width: 3px;
  height: 10px;
  border-radius: 25%;
  background-color: #004272;
  animation-name: scroll;
  animation-duration: 2.2s;
  animation-timing-function: cubic-bezier(0.15, 0.41, 0.69, 0.94);
  animation-iteration-count: infinite;
}
@keyframes scroll {
  0% {
    opacity: 0;
  }
  10% {
    transform: translateY(0);
    opacity: 1;
  }
  100% {
    transform: translateY(15px);
    opacity: 0;
  }
}

/*-----------------MOUSE ANIMATION-----------------*/

/*-----------------TO THE TOP BTN-----------------*/

.totopbtn {
  position: fixed;
  bottom: 20px;
  right: 32px;
  background-color: #004272;
  color: #f0f0f0;
  border: solid 2px #f0f0f0;
  border-radius: 50px;
  padding: 14px 19px;
  transition: 0.5s;
}
.totopbtn:hover {
  background-color: #002c4b;
}
.totopbtn a {
  color: #f0f0f0;
}
/*-----------------TO THE TOP BTN-----------------*/

/*-----------------FRONT FACE-----------------*/

.front-face-div-react {
  padding: 0;
  position: relative;
  width: 100%;
  height: 100vh;
  background-color: #f5f5f5;
}
.main-text {
  top: 50%;
  vertical-align: auto;
  text-align: center;
  position: absolute;
}
.logo {
  width: 50vh;
  transition: 0.3s;
}
.logo:hover {
  transform: scale(1.08);
}
.img-front-face {
  width: 100%;
  height: 100vh;
}
/*-----------------FRONT FACE-----------------*/

/*-----------------EQUIPE SECTION-----------------*/
.equipe-title {
  margin: 100px 0 60px 0;
}
.equipe-title > h1 {
  color: #004272;
  text-align: center;
  font-weight: 800;
}
.worker-all-tabs {
  display: grid;
  justify-content: center;
  align-content: center;
  grid-template-columns: 1fr;
  grid-row-gap: 30px;
  margin-bottom: 60px;
}

.worker-tab {
  display: block;
  width: 267px;
  max-width: 312px;
  height: 524px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.15);
  background-color: #f5f5f5;
  border-radius: 40px;
  margin-left: auto;
  margin-right: auto;
  transition: 0.3s;
}
.worker-tab:hover {
  z-index: 1000;
  transform: scale(1.04);
}
.worker-tab-content > h3 {
  margin-top: 24px;
  text-align: center;
  color: #004272;
  text-transform: uppercase;
  font-weight: 600;
}
.worker-tab-content > h6 {
  text-align: center;
  color: #004272;
}
.worker-img {
  max-width: 100%;
  height: auto;
  border-radius: 40px;
}
.worker-tab-links {
  text-align: center;
  margin: 24px auto;
}
.worker-tab-links > a {
  display: inline-block;
  color: #004272;
  padding: 10px;
}
.worker-tab-links > a:hover {
  color: #016dbb;
}
/*-----------------EQUIPE SECTION-----------------*/

/*-----------------PROJETO SECTION-----------------*/

.projeto-title {
  margin: 100px 0 60px 0;
}

.projeto-title > h1 {
  color: #004272;
  text-align: center;
  font-weight: 800;
}
.projeto-content {
  position: relative;
  background-color: #f5f5f5;
  padding: 40px 20px 20px 20px;
  border-radius: 40px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.14);
  color: #004272;
}
.projeto-content > h3 {
  text-align: center;
}
.projeto-content > p {
  margin: 40px;
  text-align: justify;
  font-size: 14px;
}
.projeto-btn-div {
  text-align: center;
}
.projeto-btn {
  padding: 16px 40px;
  border-radius: 12px;
  border: none;
  background-color: #dba111;
  color: #fff;
  transition: 0.3s;
}
.projeto-btn:hover {
  background-color: #bb890b;
}
/*-----------------PROJETO SECTION-----------------*/

/*-----------------PARCEIROS SECTION-----------------*/
.parceiros-title {
  margin: 100px 0 60px 0;
}

.parceiros-title > h1 {
  color: #004272;
  text-align: center;
  font-weight: 800;
}
.parceiros-section {
  padding: 0;
}
#parceiros-id {
  padding-left: 0 !important;
  padding-right: 0 !important;
}
.parceiros-content {
  position: relative;
  display: block;
  width: 100%;
  background-color: #ebebeb;
}
.parceiros-img-div {
  display: grid;
  justify-content: center;
  align-content: center;
  grid-template-columns: 1fr;
  grid-row-gap: 30px;
  padding: 64px;
}
@media (min-width: 550px) {
  .parceiros-img-div {
    grid-template-columns: 1fr 1fr;
  }
}
@media (min-width: 830px) {
  .parceiros-img-div {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

.parceiros-img-div > a > img {
  transition: 0.3s;
}
.parceiros-img-div > a > img:hover {
  transform: scale(1.08);
}
.imd-logo {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 58%;
}
.ufrn-logo {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 40%;
}
.synchro-logo {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 30%;
}
/*-----------------PARCEIROS SECTION-----------------*/

/*-----------------CONTACT-----------------*/
.txt-form {
  margin-top: 18px;
  text-align: center;
  color: #004272;
  font-weight: 600;
}
.contact-title {
  margin: 100px 0 60px 0;
}
.contact-title > h1 {
  color: #004272;
  text-align: center;
  font-weight: 800;
}
.contact-logo-form {
  display: flex;
}
.logo-contact {
  border-radius: 50px 0 0 0;
  width: 50%;
  height: 400px;
  background-color: #004272;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 4px 4px 20px rgba(0, 0, 0, 0.14);
}
.logo-contact > h2 {
  font-weight: 700;
  color: #fff;
}
.form {
  border-radius: 0 50px 0 0;
  background-color: #f5f5f5;
  position: relative;
  box-shadow: 0px -2px 4px rgba(0, 0, 0, 0.14);
}
.form > form {
  position: absolute;
  top: 60px;
  width: 100%;
}
.form-input-bg {
  margin: 8px 24px;
}
::placeholder {
  font-weight: 400;
  color: #004272b2;
}
.form-input-bg input {
  padding: 12px;
  background-color: #e2e2e2;
  border: transparent;
  outline: none;
  border-radius: 12px;
}
.form-message-bg textarea {
  padding: 12px;
  background-color: #e2e2e2;
  border: transparent;
  outline: none;
  resize: none;
  border-radius: 12px;
}
.form-button-div {
  margin: 8px 16px;
  margin-bottom: 32px;
}

.form-button-div input {
  width: 100%;
  background-color: #dba111;
  padding: 12px 0;
  color: #fff;
  border: none;
  border-radius: 12px;
  outline: none;
  transition: 0.3s;
}
.form-button-div input:hover {
  background-color: #bb890b;
}
/*-----------------CONTACT-----------------*/

/*-----------------BOTTOM-----------------*/
.bottom-bar {
  width: 100%;
  background-color: #004272;
  z-index: 100000;
}
.copyright-content {
  margin: 64px 0 32px 0;
  display: flex;
}
.bottom-bar-main-div {
  background-color: #004272;
}
.bottom-bar-logo > h2 {
  font-weight: 700;
  color: #fff;
  text-align: center;
}
.copyright p {
  color: rgba(255, 255, 255, 0.2);
  text-align: center;
  font-weight: 500;
  font-size: 10px;
  margin-bottom: 32px;
}
.line {
  width: 6%;
  height: 0px;
  display: block;
  border-top: solid thin #aaaaaa5b;
  margin: 24px auto 24px auto;
}
/*-----------------BOTTOM-----------------*/
@media (min-width: 200px) and (max-width: 549px) {
  .totopbtn {
    bottom: 2%;
    right: 6%;
  }
  .projeto-content > p {
    margin: 12px;
    text-align: justify;
    font-size: 14px;
  }
  .logo-menu-mobile {
    display: block;
  }
  .icon-menu-mobile {
    font-size: 14px;
  }
  #interactiveMenu {
    display: block;
    z-index: 1001;
    position: fixed;
    top: 2%;
    right: 8%;
  }
  nav {
    transition: 0.5s;
    position: fixed;
    left: -50%;
    width: 50%;
    height: 100%;
  }
  .menu-nav {
    padding-left: 0;
    display: block;
  }
  .navbar {
    display: block;
  }
  .item-nav {
    width: 100%;
  }
  .img-front-face {
    display: none;
  }
  .logo {
    width: 32vh;
  }
  .form-input-bg {
    margin: 10px 16px;
  }
  .form-input-bg input {
    width: 100%;
  }
  .form-message-bg {
    margin: 10px 16px;
  }
  .form-message-bg textarea {
    width: 100%;
  }
  .form > form {
    position: relative;
    top: 32px;
  }
  .logo-contact {
    display: none;
  }
  .form {
    width: 100%;
    border-radius: 50px 50px 0 0;
    margin-bottom: 20px;
  }
}
@media (min-width: 550px) and (max-width: 829px) {
  .totopbtn {
    bottom: 2%;
    right: 6%;
  }
  .projeto-content > p {
    margin: 12px;
    text-align: justify;
    font-size: 14px;
  }
  .logo-menu-mobile {
    display: block;
  }
  #interactiveMenu {
    display: block;
    z-index: 1001;
    position: fixed;
    top: 2%;
    right: 8%;
  }
  nav {
    transition: 0.5s;
    position: fixed;
    left: -50%;
    width: 50%;
    height: 100%;
  }
  .img-front-face {
    display: none;
  }
  .menu-nav {
    padding-left: 0;
    display: block;
  }
  .navbar {
    display: block;
  }
  .item-nav {
    width: 100%;
  }
  .form-input-bg input {
    width: 100%;
  }
  .form-message-bg textarea {
    width: 100%;
  }
  .form-message-bg {
    margin: 8px 24px;
  }
  .worker-all-tabs {
    grid-template-columns: 1fr 1fr;
  }
  .form-button-div {
    margin: 8px 24px;
  }
}
@media (min-width: 830px) {
  nav {
    width: 100%;
    left: 0%;
  }
  .worker-all-tabs {
    grid-template-columns: 1fr 1fr 1fr;
  }
  .form-input-bg input {
    width: 100%;
  }
  .form-message-bg textarea {
    width: 100%;
  }
  .form-message-bg {
    margin: 8px 24px;
  }
  .worker-all-tabs {
    grid-template-columns: 1fr 1fr;
  }
  .form-button-div {
    margin: 8px 24px;
  }
}
@media (min-width: 1080px) {
  .worker-all-tabs {
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
